import React from 'react'
import Review from './Review'
import '../styles/reviewsSection.css'


function ReviewsSection(){
    return(
        
        <section class="reviews">
        <div class="reviews-container">

            <Review 
                src={require("../assets/tapas/TAPA PRINCIPITO ALTA-08 (1).jpg")} 
                alt="el-principito" 
                title="EL PRINCIPITO"
                review="Hermosa narracin!! Me encant!!"
            />
            <Review 
                src={require("../assets/tapas/tapa-siddartha.jpeg")} 
                alt="siddhartha" 
                title="SIDDARTHA"
                review="Uno de los cuentos ms bonitos que he escuchado. Me lleg al alma y al corazn."
            />
            <Review 
                src={require("../assets/tapas/el-lobo-tapa.jpg")} 
                alt="el-lobo" 
                title="EL LOBO ESTEPARIO"
                review="Increble relato y ambiente!"
            />     
        </div>
    </section>
        
    )
}

export default ReviewsSection